.usersPageWrapper {
    display: flex;
    justify-content: center;
    position: relative;
    text-align: center;
}

.userPageContainer {
    width: 80%;
    align-items: center;
}

.userWrapper {
    margin-top: 10px;
}

.holiday {
     overflow: hidden;
     font-size: 16px;
 }
.holiday dt, .holiday dd {
    height: 2.5em;
    line-height: 2.5em;
    padding: 0 0.625em 0 0.875em;
    color: #4C565C;
    box-sizing: border-box;
}
dt {
    width: 30%;
    float: left;
    clear: right;
    background: #D3E6DD;
    font-weight: bold;
}
dd {
    width: 70%;
    float: right;
    margin-left: 0;
    margin-bottom: .3125em;
    border: 1px solid #BECFC7;
    border-left: none;
}
